import { Component, OnInit } from '@angular/core';
import { BeersService } from '../beers.service';
import { ActivatedRoute } from '@angular/router';
declare const Highcharts: any;
import { SelectItem } from 'primeng/components/common/selectitem';

@Component({
  selector: 'app-beer-details',
  templateUrl: './beer-details.component.html',
  styleUrls: ['./beer-details.component.css']
})
export class BeerDetailsComponent implements OnInit {

  beerName: string;

 

  constructor(
    private beerService: BeersService,
    private route: ActivatedRoute
  ) {
    this.route.paramMap.subscribe((paramMap) => {
      this.beerName = paramMap.get('beer');

      this.beerService.getBarsSelling(this.beerName).subscribe(
          data => {
            //
            const name = [];
            const totals = [];
            data.forEach(item => {
            //console.log(data)
            name.push(item.name);
            //console.log(name)
            totals.push(item.total);
            });
  
            // use the line below to render the graph
            this.renderChart1(name, totals);
          }
        );

      this.beerService.getBeerConsumer(this.beerName).subscribe(
          data => {
            const name = [];
            const totals = [];
            data.forEach(item => {
              
            name.push(item.name);
          
            totals.push(item.total);
            });
            ;
            // use the line below to render the graph
            this.renderChart2(name, totals);
          }
        );

        this.beerService.getBeerDay(this.beerName).subscribe(
          data => {
            const name = [];
            const totals = [];
            data.forEach(item => {
              
            name.push(item.day);
          
            totals.push(item.total);
            });
            ;
            // use the line below to render the graph
            this.renderChart3(name, totals);
          }
        );

    });
  }

  renderChart1(name: string[], totals: Number[]) {
    
    Highcharts.chart('bargraph1', {
    chart: {
    type: 'column'
    },
    title: {
    text: 'Bars that Sell the Most'
    },
    xAxis: {
    categories: name,
    title: {
    text: 'Bars'
    }
    },
    yAxis: {
    min: 0,
    title: {
    text: 'Total Amount Sold'
    },
    labels: {
    overflow: 'justify'
    }
    },
    plotOptions: {
    bar: {
    dataLabels: {
    enabled: true
    }
    }
    },
    legend: {
    enabled: false
    },
    credits: {
    enabled: false
    },
    series: [{
    data: totals
    }]
    });
    
    }
    renderChart2(name: string[], totals: Number[]) {
    
      Highcharts.chart('bargraph2', {
      chart: {
      type: 'column'
      },
      title: {
      text: 'Drinkers Who are Biggest Consumers'
      },
      xAxis: {
      categories: name,
      title: {
      text: 'Drinkers'
      }
      },
      yAxis: {
      min: 0,
      title: {
      text: 'Total Amount Consumed'
      },
      labels: {
      overflow: 'justify'
      }
      },
      plotOptions: {
      bar: {
      dataLabels: {
      enabled: true
      }
      }
      },
      legend: {
      enabled: false
      },
      credits: {
      enabled: false
      },
      series: [{
      data: totals
      }]
      });
      
      }
      renderChart3(name: string[], totals: Number[]) {
    
        Highcharts.chart('bargraph3', {
        chart: {
        type: 'column'
        },
        title: {
        text: 'Most Popular Day'
        },
        xAxis: {
        categories: name,
        title: {
        text: 'Day'
        }
        },
        yAxis: {
        min: 0,
        title: {
        text: 'Total Amount Sold'
        },
        labels: {
        overflow: 'justify'
        }
        },
        plotOptions: {
        bar: {
        dataLabels: {
        enabled: true
        }
        }
        },
        legend: {
        enabled: false
        },
        credits: {
        enabled: false
        },
        series: [{
        data: totals
        }]
        });
        
        }
  ngOnInit() {
  }



}