import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DrinkersService, Drinker, InfoT } from '../drinkers.service';
import { HttpResponse } from '@angular/common/http';
declare const Highcharts: any;

@Component({
  selector: 'app-drinker-details',
  templateUrl: './drinker-details.component.html',
  styleUrls: ['./drinker-details.component.css']
})
export class DrinkerDetailsComponent implements OnInit {


  drinkerName: string;
  drinkerDetails: Drinker;
  drinkerInfo: InfoT[];

  constructor(
    private drinkerService: DrinkersService,
    private route: ActivatedRoute
  ) {
    route.paramMap.subscribe((paramMap) => {
      this.drinkerName = paramMap.get('drinker');
      console.log(this.drinkerName)
      drinkerService.getDrinker(this.drinkerName).subscribe(
        data => {
          this.drinkerDetails = data;
        },
        (error: HttpResponse<any>) => {
          if (error.status === 404) {
            alert('Bar not found');
          } else {
            console.error(error.status + ' - ' + error.body);
            alert('An error occurred on the server. Please check the browser console.');
          }
        }
      );

      drinkerService.getDrinkerTrans(this.drinkerName).subscribe(
        data => {
          console.log(data)
          this.drinkerInfo = data;
        }
      );

      drinkerService.getDrinkerBills(this.drinkerName).subscribe(
        data => {
          //
          const name = [];
          const totals = [];
          data.forEach(item => {
            
          name.push(item.bar);
         
          totals.push(item.total);
          });
          ;
          // use the line below to render the graph
          this.renderChart1(name, totals);
        }
      );

      

      drinkerService.getDrinkerLikes(this.drinkerName).subscribe(
        data => {
          //
          const name = [];
          const totals = [];
          data.forEach(item => {
          
          name.push(item.beer);
          totals.push(item.total);
          });
          ;
          // use the line below to render the graph
          this.renderChart2(name, totals);
        }
      );

      
    });
  }

  renderChart1(name: string[], totals: Number[]) {
    
    Highcharts.chart('bargraph1', {
    chart: {
    type: 'column'
    },
    title: {
    text: 'Spending in different Bars'
    },
    xAxis: {
    categories: name,
    title: {
    text: 'Bars'
    }
    },
    yAxis: {
    min: 0,
    title: {
    text: 'Total Amount Spent'
    },
    labels: {
    overflow: 'justify'
    }
    },
    plotOptions: {
    bar: {
    dataLabels: {
    enabled: true
    }
    }
    },
    legend: {
    enabled: false
    },
    credits: {
    enabled: false
    },
    series: [{
    data: totals
    }]
    });
    
    }
    renderChart2(name: string[], totals: Number[]) {
    
      Highcharts.chart('bargraph2', {
      chart: {
      type: 'column'
      },
      title: {
      text: 'Most Ordered Beers'
      },
      xAxis: {
      categories: name,
      title: {
      text: 'Beer'
      }
      },
      yAxis: {
      min: 0,
      title: {
      text: 'Total Amount'
      },
      labels: {
      overflow: 'justify'
      }
      },
      plotOptions: {
      bar: {
      dataLabels: {
      enabled: true
      }
      }
      },
      legend: {
      enabled: false
      },
      credits: {
      enabled: false
      },
      series: [{
      data: totals
      }]
      });
      
      }

  ngOnInit() {
  }

}
