import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BarsService, Bar, BarMenuItem, Spend } from '../bars.service';
import { HttpResponse } from '@angular/common/http';
declare const Highcharts: any;

@Component({
  selector: 'app-bar-details',
  templateUrl: './bar-details.component.html',
  styleUrls: ['./bar-details.component.css']
})
export class BarDetailsComponent implements OnInit {

  barName: string;
  barDetails: Bar;
  

  constructor(
    private barService: BarsService,
    private route: ActivatedRoute
  ) {

    route.paramMap.subscribe((paramMap) => {
      this.barName = paramMap.get('bar');

      barService.getBar(this.barName).subscribe(
        data => {
          this.barDetails = data;
        },
        (error: HttpResponse<any>) => {
          if (error.status === 404) {
            alert('Bar not found');
          } else {
            console.error(error.status + ' - ' + error.body);
            alert('An error occurred on the server. Please check the browser console.');
          }
        }
      );
      
      barService.getTopSpenders(this.barName).subscribe(
        data => {
          //
          const name = [];
          const totals = [];
          data.forEach(item => {
            
          name.push(item.name);
          totals.push(item.grand);
          });
          ;
          // use the line below to render the graph
          this.renderChart1(name, totals);
        }
      );

      
      
      barService.getTopSellers(this.barName).subscribe(
        data => {
          
          const name = [];
          const totals = [];
          data.forEach(item => {
            
          name.push(item.beer);
          totals.push(item.quantity);
          });
          ;
          // use the line below to render the graph
          this.renderChart2(name, totals);
        }
      );

      barService.getTopSellers(this.barName).subscribe(
        data => {
          
          const name = [];
          const totals = [];
          data.forEach(item => {
            
          name.push(item.seller);
          totals.push(item.quantity);
          });
          ;
          // use the line below to render the graph
          this.renderChart3(name, totals);
        }
      );

      barService.getBusyTime(this.barName).subscribe(
        data => {
          
          const name = [];
          const totals = [];
          data.forEach(item => {
            
          name.push(item.day);
          totals.push(item.count);
          });
          ;
          // use the line below to render the graph
          this.renderChart4(name, totals);
        }
      );
      
      barService.getBusyWeek(this.barName).subscribe(
        data => {
          
          const name = [];
          const totals = [];
          data.forEach(item => {
            
          name.push(item.date);
          totals.push(item.count);
          });
          ;
          // use the line below to render the graph
          this.renderChart5(name, totals);
        }
      );
      
    });
  }

  renderChart1(drinkers: string[], dtotals: Number[]) {
    
    Highcharts.chart('bargraph1', {
    chart: {
    type: 'column'
    },
    title: {
    text: 'Top Spenders'
    },
    xAxis: {
    categories: drinkers,
    title: {
    text: 'Drinkers'
    }
    },
    yAxis: {
    min: 0,
    title: {
    text: 'Total Amount Spent'
    },
    labels: {
    overflow: 'justify'
    }
    },
    plotOptions: {
    bar: {
    dataLabels: {
    enabled: true
    }
    }
    },
    legend: {
    enabled: false
    },
    credits: {
    enabled: false
    },
    series: [{
    data: dtotals
    }]
    });
    
    }

    renderChart2(beers: string[], btotals: Number[]) {
    
      
      Highcharts.chart('bargraph2', {
        chart: {
        type: 'column'
        },
        title: {
        text: 'Top Beers'
        },
        xAxis: {
        categories: beers,
        title: {
        text: 'Beers'
        }
        },
        yAxis: {
        min: 0,
        title: {
        text: 'Quantity Sold'
        },
        labels: {
        overflow: 'justify'
        }
        },
        plotOptions: {
        bar: {
        dataLabels: {
        enabled: true
        }
        }
        },
        legend: {
        enabled: false
        },
        credits: {
        enabled: false
        },
        series: [{
        data: btotals
        }]
        });
      }
      renderChart3(manf: string[], mtotals: Number[]) {
    
      
        Highcharts.chart('bargraph3', {
          chart: {
          type: 'column'
          },
          title: {
          text: 'Top Sellers'
          },
          xAxis: {
          categories: manf,
          title: {
          text: 'Manufacturers'
          }
          },
          yAxis: {
          min: 0,
          title: {
          text: 'Quantity Sold'
          },
          labels: {
          overflow: 'justify'
          }
          },
          plotOptions: {
          bar: {
          dataLabels: {
          enabled: true
          }
          }
          },
          legend: {
          enabled: false
          },
          credits: {
          enabled: false
          },
          series: [{
          data: mtotals
          }]
          });
        }

        renderChart4(days: string[], atotals: Number[]) {
         
      
          Highcharts.chart('bargraph4', {
            chart: {
            type: 'column'
            },
            title: {
            text: 'Busiest Days'
            },
            xAxis: {
            categories: days,
            title: {
            text: 'Week Days'
            }
            },
            yAxis: {
            min: 0,
            title: {
            text: 'Quantity Sold'
            },
            labels: {
            overflow: 'justify'
            }
            },
            plotOptions: {
            bar: {
            dataLabels: {
            enabled: true
            }
            }
            },
            legend: {
            enabled: false
            },
            credits: {
            enabled: false
            },
            series: [{
            data: atotals
            }]
            });
          }

          renderChart5(weeks: string[], wtotals: Number[]) {
    
      
            Highcharts.chart('bargraph5', {
              chart: {
              type: 'column'
              },
              title: {
              text: 'Busiest Weeks'
              },
              xAxis: {
              categories: weeks,
              title: {
              text: 'Weeks'
              }
              },
              yAxis: {
              min: 0,
              title: {
              text: 'Quantity Sold'
              },
              labels: {
              overflow: 'justify'
              }
              },
              plotOptions: {
              bar: {
              dataLabels: {
              enabled: true
              }
              }
              },
              legend: {
              enabled: false
              },
              credits: {
              enabled: false
              },
              series: [{
              data: wtotals
              }]
              });
            }

    

  ngOnInit() {
  }

}