import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';



export interface Beer {
  name: string;
  manf: string;
}



@Injectable({
  providedIn: 'root'
})
export class BeersService {

  constructor(private http: HttpClient) { }

  getBeers() {
    return this.http.get<Beer[]>('/api/beer');
  }

  getBarsSelling(name: string) {
    return this.http.get<any[]>('/api/beerselling/'+ name);
  }

  getBeerConsumer(name: string){
    return this.http.get<any[]>('/api/beerconsumer/' + name);
  }

  getBeerDay(name: string){
    return this.http.get<any[]>('/api/beerday/'+ name);
  }

}