import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';


export interface Drinker {
  name: string;
  state: string;
  city: string;
  phone: string;
  address: string;
}


export interface InfoT{
  bar: string;
  bill: string;
  name: string;
  quantity: string;
  date: string;
  time: string;
}

export interface InfoB{
  bar: string;
  bill: string;
  total: string;
  tax: string;
  tip: string;
  date: string;
  time: string;
}

export interface Likes {
  name: string;
  total: string;
}


@Injectable({
  providedIn: 'root'
})

export class DrinkersService {

  constructor(
    public http: HttpClient
  ) { }

  getDrinkers() {
    return this.http.get<Drinker[]>('/api/drinker');
  }

  getDrinker(name: string) {
    return this.http.get<Drinker>('/api/drinker/'+ name);
  }

  getDrinkerTrans(name: string) {
    return this.http.get<InfoT[]>('/api/drinkertran/'+ name);
  }

  getDrinkerBills(name: string) {
    return this.http.get<any[]>('/api/drinkerbill/'+ name);
  }

  getDrinkerLikes(name: string) {
    return this.http.get<any[]>('/api/likes/'+ name);
  }
  
}
