import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ModificationsService, output, Mod1, Mod2, Mod3} from '../modifications.service';

@Component({
  selector: 'app-modification',
  templateUrl: './modification.component.html',
  styleUrls: ['./modification.component.css'],
 //template: `<Button (click) = 'onClick()'> Click Me</Button>`
})
export class ModificationComponent implements OnInit {
  out: string;
  query: string;
  mod1: Mod1[];
  mod2: Mod2[];
  mod3: Mod3[];
  

  constructor(
    private modificationService: ModificationsService,
    private route: ActivatedRoute
  ) {
    
    this.getMod2();
    this.getMod1();

      

  }
  getMod1(){

    this.modificationService.getModifications1().subscribe(
      data => {
        console.log(data)
        this.mod1 = data;
      }
    );
  }

  getMod2(){

    this.modificationService.getModifications2().subscribe(
      data => {

        this.mod2 = data;
      }
    );
  }
  onClick(inp:string){
    this.query=inp
    console.log(this.query)
    this.modificationService.getModification(this.query).subscribe(
      
      data => {
        this.out = JSON.stringify(data);
        console.log(this.out)
      }
    );
    
  }
  
  

  ngOnInit() {
  }

}
