import { Injectable } from '@angular/core';

import { HttpClient } from '@angular/common/http';


export interface output{
  output: string;
}

export interface Mod1{
  ID: string;
  bar: string;
  bar_state: string;
  drinker_state: string;
  drinker: string;
}

export interface Mod2{
  ID: string;
  name: string;
  open: string;
  close: string;
  time: string;
}

export interface Mod3{
  ID: string;
  bar: string;
  bar_state: string;
  drinker_state: string;
  drinker: string;
}


@Injectable({
  providedIn: 'root'
})
export class ModificationsService {

  constructor(
    public http: HttpClient
  ) { }

  getModifications1() {
    
    return this.http.get<Mod1[]>('/api/modification1');
  }
  getModifications2() {

    return this.http.get<Mod2[]>('/api/modification2');
  }
  getModifications3() {
    return this.http.get<Mod3[]>('/api/modification3');
  }
  getModification(query: string ) {
    return this.http.get<output>('/api/modification/' + query);
  }
}
