import { Injectable, SystemJsNgModuleLoader } from '@angular/core';

import { HttpClient } from '@angular/common/http';



export interface Bar {
  name: string;
  license: string;
  state: string;
  city: string;
  phone: string;
  address: string;
}

export interface BarMenuItem {
  beer: string;
  manf: string;
}

export interface Spend {
  name: string;
  total: number;
}

export interface Beers {
  name: string;
  seller: string;
}


@Injectable({
  providedIn: 'root'
})
export class BarsService {

  constructor(
    public http: HttpClient
  ) { }

  getBars() {
    return this.http.get<Bar[]>('/api/bar');
  }

  getBar(bar: string) {
    return this.http.get<Bar>('/api/bar/' + bar);
  }

  getMenu(bar: string) {
    return this.http.get<BarMenuItem[]>('/api/menu/' + bar);
  }

  getTopSpenders(bar: string) {
    return this.http.get<any[]>('/api/barspender/' + bar);
  }

  getTopSellers(bar: string) {
    return this.http.get<any[]>('/api/barseller/' + bar);
  }

  getBusyTime(bar: string) {
    return this.http.get<any[]>('/api/bartime/' + bar);
  }

  getBusyWeek(bar: string) {
    return this.http.get<any[]>('/api/barweek/' + bar);
  }
}
